import React, { useEffect, useContext } from "react";
import { useStyles } from "./styles";

import {
  Button,
  Container,
  CssBaseline,
  Box,
  Typography,
} from "@material-ui/core";

import { api } from "../../utils/api";
import { useHistory } from "react-router-dom";

import { UserContext } from "../../Contexts/userContext";

export default function Profile() {
  const classes = useStyles();
  const history = useHistory();

  const { username } = useContext(UserContext);

  const handleAddMatches = () => {
    history.push("/addmatches");
  };

  const handleShowMatches = () => {
    history.push("/matches");
  };

  const handleMyMatches = () => {
    history.push("/mymatches");
  };

  const handleRanking = () => {
    history.push("/ranking");
  };

  const handleEuroWinner = () => {
    history.push("/eurowinner");
  };

  const handleRealEuroWinner = () => {
    history.push("/realeurowinner");
  };

  return (
    <Container className={classes.container}>
      <CssBaseline />
      <Typography style={{ marginLeft: "auto", marginRight: "3em" }}>
        {username}
      </Typography>
      <Box className={classes.buttonContainer}>
        <Button
          className={classes.button}
          variant="contained"
          color="primary"
          onClick={handleShowMatches}
        >
          All matches
        </Button>
        <Button
          className={classes.button}
          variant="contained"
          color="primary"
          onClick={handleMyMatches}
        >
          My matches
        </Button>

        <Button
          className={classes.button}
          variant="contained"
          color="primary"
          onClick={handleEuroWinner}
        >
          My Euro Winner
        </Button>
        <Button
          className={classes.button}
          variant="contained"
          color="primary"
          onClick={handleRanking}
        >
          Ranking
        </Button>
        {/* <Button
          className={classes.button}
          variant="contained"
          color="primary"
          onClick={handleAddMatches}
        >
          Add matches
        </Button> */}
        <Button
          className={classes.button}
          variant="contained"
          color="secondary"
          onClick={handleRealEuroWinner}
        >
          Euro winner is...
        </Button>
      </Box>
    </Container>
  );
}
