import React, { useEffect, useState } from "react";

import { api } from "../../utils/api";
import { useHistory } from "react-router-dom";
import {
  Box,
  Container,
  Paper,
  Typography,
  Button,
  Modal,
  TextField,
  FormControl,
  InputLabel,
  Select,
} from "@material-ui/core";
import EditIcon from "@material-ui/icons/Edit";

function Match(props) {
  const history = useHistory();
  const { id, homeGoals, awayGoals, result, match } = props;

  const [open, setOpen] = useState(false);
  const [homeGoalsState, setHomeGoalsState] = useState("");
  const [awayGoalsState, setAwayGoalsState] = useState("");
  const [resultState, setResultState] = useState("");

  const handleOpen = () => {
    const matchDate = new Date(match.date);
    const currentDate = new Date();
    if (currentDate.getTime() - matchDate.getTime() < 0) {
      setOpen(true);
    }
  };

  const handleClose = () => {
    setOpen(false);
  };

  const saveResult = () => {
    api
      .put(
        `/usermatches/${id}`,
        {
          homeGoals: homeGoalsState,
          awayGoals: awayGoalsState,
          result: resultState,
        },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      )
      .then((res) => {
        console.log(res.data.message);
        window.location.reload();
      })
      .catch((_err) => {
        console.log("error", _err);
      });
  };

  const formatTime = (d) => {
    const date = new Date(d);
    const day = date.getDate();
    const month = date.getMonth() + 1;
    const hours = date.getHours();
    let minutes = date.getMinutes();
    if (minutes === 0) {
      minutes = "00";
    }

    return `${day}/${month} ${hours}:${minutes}`;
  };

  const handlaResultChange = (event) => {
    setResultState(event.target.value);
  };

  return (
    <Container>
      <Paper
        onClick={handleOpen}
        style={{
          display: "flex",
          flexDirection: "column",
          marginBottom: "0.5em",
          cursor: "pointer",
        }}
      >
        <Typography variant="h5" style={{ margin: "auto", fontWeight: "bold" }}>
          {formatTime(match.date)}
        </Typography>
        <Typography
          style={{ margin: "auto", marginBottom: "1em" }}
        >{`Group: ${match.group}`}</Typography>
        <Box
          style={{
            display: "flex",
            justifyContent: "space-around",
            marginBottom: "0.5em",
            alignItems: "center",
          }}
        >
          <Typography>{`${match.homeTeam.country}`}</Typography>
          <Typography
            variant="h4"
            style={{ fontWeight: "bold", color: "#00FF00" }}
          >
            {homeGoals ? `${homeGoals}` : `-`}
          </Typography>
          <Typography variant="h6">:</Typography>

          <Typography
            variant="h4"
            style={{ fontWeight: "bold", color: "#00FF00" }}
          >
            {awayGoals ? `${awayGoals}` : `-`}
          </Typography>
          <Typography>{`${match.awayTeam.country}`}</Typography>
        </Box>
        {result ? (
          <Typography
            variant="h5"
            style={{
              margin: "auto",
              marginTop: "1em",
              marginBottom: "1em",
              color: "#00AA00",
              fontWeight: "bold",
            }}
          >{`My result: ${result}`}</Typography>
        ) : (
          <Typography
            variant="h5"
            style={{
              margin: "auto",
              marginTop: "1em",
              marginBottom: "1em",
              color: "#00AA00",
              fontWeight: "bold",
            }}
          >{`My result: -`}</Typography>
        )}
      </Paper>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <Paper style={{ padding: "3em" }}>
          <Box
            style={{
              display: "flex",
              justifyContent: "space-around",
              marginBottom: "0.5em",
              alignItems: "center",
            }}
          >
            <Typography>{`${match.homeTeam.country}`}</Typography>
            <TextField
              type="number"
              variant="outlined"
              style={{ width: "60px", marginLeft: "1em", marginRight: "1em" }}
              value={homeGoalsState}
              onInput={(e) => setHomeGoalsState(e.target.value)}
            />
            <Typography variant="h6">:</Typography>

            <TextField
              type="number"
              variant="outlined"
              style={{ width: "60px", marginLeft: "1em", marginRight: "1em" }}
              value={awayGoalsState}
              onInput={(e) => setAwayGoalsState(e.target.value)}
            />
            <Typography>{`${match.awayTeam.country}`}</Typography>

            <FormControl style={{ marginLeft: "1em", width: "5em" }}>
              <InputLabel>Winner</InputLabel>

              <Select
                native
                value={resultState}
                onChange={handlaResultChange}
                inputProps={{
                  name: "Home",
                }}
              >
                <option aria-label="None" value="" />
                <option value="1">1</option>
                <option value="0">0</option>
                <option value="2">2</option>
              </Select>
            </FormControl>
          </Box>
          <Button
            type="submit"
            variant="contained"
            color="primary"
            onClick={() => {
              saveResult();
              handleClose();
            }}
          >
            Save
          </Button>
        </Paper>
      </Modal>
    </Container>
  );
}

export default function MyMatches() {
  const history = useHistory();

  const [matches, setMatches] = useState([]);

  useEffect(() => {
    getMyMatches();
  }, []);

  const getMyMatches = () => {
    api
      .get("/usermatches", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      })
      .then((res) => {
        setMatches(res.data.matches);
        console.log(res.data);
      });
  };

  return (
    <>
      {matches.map((e, i) => {
        return (
          <Match
            id={e._id}
            key={i}
            match={e.match}
            result={e.result}
            homeGoals={e.homeGoals}
            awayGoals={e.awayGoals}
          />
        );
      })}
    </>
  );
}
