import React, { useEffect, useState } from "react";

import { api } from "../../utils/api";
import { useHistory } from "react-router-dom";
import {
  Box,
  Container,
  Paper,
  Typography,
  Button,
  Modal,
  TextField,
} from "@material-ui/core";
import EditIcon from "@material-ui/icons/Edit";

function Match(props) {
  const history = useHistory();
  const { id, date, homeTeam, awayTeam, homeGoals, awayGoals, group } = props;

  const [open, setOpen] = useState(false);
  const [homeGoalsState, setHomeGoalsState] = useState("");
  const [awayGoalsState, setAwayGoalsState] = useState("");

  const handleOpen = () => {
    // const matchDate = new Date(date);
    // const currentDate = new Date();
    // if (currentDate.getTime() - matchDate.getTime() < 0) {
    //   setOpen(true);
    // }
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const saveResult = () => {
    api
      .put(
        `/matches/${id}`,
        { homeGoals: homeGoalsState, awayGoals: awayGoalsState },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      )
      .then((res) => {
        console.log(res.data.message);
        window.location.reload();
      })
      .catch((_err) => {
        console.log("error", _err);
      });
  };

  const formatTime = (d) => {
    const date = new Date(d);
    const day = date.getDate();
    const month = date.getMonth() + 1;
    const hours = date.getHours();
    const minutes = date.getMinutes();

    return `${day}/${month} ${hours}:${minutes}`;
  };

  return (
    <Container>
      <Paper
        onClick={handleOpen}
        style={{
          display: "flex",
          flexDirection: "column",
          marginBottom: "0.5em",
          cursor: "pointer",
        }}
      >
        <Typography variant="h5" style={{ margin: "auto", fontWeight: "bold" }}>
          {formatTime(date)}
        </Typography>
        <Typography
          style={{ margin: "auto", marginBottom: "1em" }}
        >{`Group: ${group}`}</Typography>
        <Box
          style={{
            display: "flex",
            justifyContent: "space-around",
            marginBottom: "0.5em",
            alignItems: "center",
          }}
        >
          <Typography>{`${homeTeam.country}`}</Typography>
          <Typography
            variant="h6"
            style={{ fontWeight: "bold", color: "#FF0000" }}
          >
            {homeGoals ? `${homeGoals}` : `-`}
          </Typography>
          <Typography variant="h6">:</Typography>

          <Typography
            variant="h6"
            style={{ fontWeight: "bold", color: "#FF0000" }}
          >
            {awayGoals ? `${awayGoals}` : `-`}
          </Typography>
          <Typography>{`${awayTeam.country}`}</Typography>
        </Box>
      </Paper>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <Paper style={{ padding: "3em" }}>
          <Box
            style={{
              display: "flex",
              justifyContent: "space-around",
              marginBottom: "0.5em",
              alignItems: "center",
            }}
          >
            <Typography>{`${homeTeam.country}`}</Typography>
            <TextField
              type="number"
              variant="outlined"
              style={{ width: "60px", marginLeft: "1em", marginRight: "1em" }}
              value={homeGoalsState}
              onInput={(e) => setHomeGoalsState(e.target.value)}
            />
            <Typography variant="h6">:</Typography>

            <TextField
              type="number"
              variant="outlined"
              style={{ width: "60px", marginLeft: "1em", marginRight: "1em" }}
              value={awayGoalsState}
              onInput={(e) => setAwayGoalsState(e.target.value)}
            />
            <Typography>{`${awayTeam.country}`}</Typography>
          </Box>
          <Button
            type="submit"
            variant="contained"
            color="primary"
            onClick={() => {
              saveResult();
              handleClose();
            }}
          >
            Save
          </Button>
        </Paper>
      </Modal>
    </Container>
  );
}

export default function Matches() {
  const history = useHistory();

  const [matches, setMatches] = useState([]);

  useEffect(() => {
    getMatches();
  }, []);

  const getMatches = () => {
    api
      .get("/matches", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      })
      .then((res) => {
        setMatches(res.data.matches);
        console.log(res.data);
      });
  };

  return (
    <>
      {matches.map((e, i) => {
        return (
          <Match
            id={e._id}
            key={i}
            group={e.group}
            homeTeam={e.homeTeam}
            awayTeam={e.awayTeam}
            homeGoals={e.homeGoals}
            awayGoals={e.awayGoals}
            date={e.date}
          />
        );
      })}
    </>
  );
}
