import React, { useEffect, useState } from "react";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";

import { api } from "../../utils/api";

export default function Ranking() {
  const CORRECT_RESULT = 1;
  const CORRECT_GOALS = 3;
  const CORRECT_WINNER = 20;

  const [ranking, setRanking] = useState([]);
  const [rows, setRows] = useState([]);

  useEffect(() => {
    let isMounted = true;
    if (isMounted) {
      async function fetchData() {
        await fetchRanking();
      }
      fetchData();
    }
    return () => {
      isMounted = false;
    };
  }, []);

  const addEuroWinnerScore = async () => {
    return await api
      .get("/realeurowinner", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      })
      .then((res) => {
        if (res.data.winner) {
          return api
            .get("/eurowinner", {
              headers: {
                Authorization: `Bearer ${localStorage.getItem("token")}`,
              },
            })
            .then((res2) => {
              console.log("res", res.data.winner.country);
              console.log("res2", res2.data.eurowinner.team.country);
              if (res2.data.eurowinner) {
                if (
                  res.data.winner.country === res2.data.eurowinner.team.country
                ) {
                  return CORRECT_WINNER;
                } else {
                  return 0;
                }
              } else {
                return 0;
              }
            });
        } else {
          return 0;
        }
      });
  };

  const addScore = (matches) => {
    let res = 0;
    matches.map((e) => {
      if (
        "result" in e.match &&
        "result" in e &&
        e.match.homeGoals &&
        e.match.awayGoals &&
        e.homeGoals &&
        e.awayGoals
      ) {
        if (
          e.match.homeGoals === e.homeGoals &&
          e.match.awayGoals === e.awayGoals
        ) {
          res += CORRECT_GOALS;
        }
        if (e.match.result === e.result) {
          res += CORRECT_RESULT;
        }
      }
    });
    return res;
  };

  const fetchRanking = async () => {
    let newArr = [...ranking];
    let newRows = [...rows];
    api
      .get("/users", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      })
      .then((res) => {
        res.data.users.map((e) => {
          let login = e.login;
          api
            .get(`/usermatchesranking/${e.login}`, {
              headers: {
                Authorization: `Bearer ${localStorage.getItem("token")}`,
              },
            })
            .then(async (res) => {
              let score = addScore(res.data.matches);
              const score2 = await addEuroWinnerScore();
              score += score2;
              setRanking((newArr) => [...newArr, { [login]: score }]);
              //newArr.push({ [login]: score });

              console.log("User successfully added to ranking");
            });
        });
      });

    setRows(() => [...newRows]);
  };

  return (
    <>
      {console.log(ranking)}
      <TableContainer
        component={Paper}
        style={{ width: "650px", margin: "auto" }}
      >
        <Table aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>Player</TableCell>
              <TableCell align="right">Score</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {ranking.map((r) => (
              <TableRow key={Object.keys(r)[0]}>
                <TableCell component="th" scope="row">
                  {Object.keys(r)[0]}
                </TableCell>
                <TableCell align="right">{r[Object.keys(r)[0]]}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  );
}
